:host {
  display: flex;
  flex-grow: 1;
  position: relative;
  align-items: center;
  //background: blue;

  .container {
    position: absolute;
    width: 100%;
    //background: red;
    display: flex;
    align-items: center;

    ::ng-deep {
      & > * {
        display: block;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        flex-grow: 1;
      }
    }
  }
}
